<script>
	export default {
		name: "MyResume",
		data() {
			return {
				user_avatar: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
			};
		},
    methods:{
      imageUrlToBase64() {
        //let
        let image = new Image();
        //
        image.setAttribute('crossOrigin', 'anonymous');
        let imageUrl = this.user_avatar;
        image.src = imageUrl
        let that = this;
        //image.onload
        image.onload = () => {
          var canvas = document.createElement("canvas");
          canvas.width = image.width;
          canvas.height = image.height;
          var context = canvas.getContext('2d');
          context.drawImage(image, 0, 0, image.width, image.height);
          var quality = 0.8;
          //dataurlbase64
          var dataURL = canvas.toDataURL("image/jpeg", quality);//toDataUrljpeg,pngpngbase64
          this.user_avatar=dataURL
        }
      },
    },
    mounted(){
      this.imageUrlToBase64();
    }
	}
</script>

