<template>
    <div ref="chart"  style="width: 600px;height:400px;"></div>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "WebEcharts",
  data () {
    return {};
  },
  methods: {
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);

      // 
      myChart.setOption({
        tooltip: {
          trigger: 'item',
          triggerOn: 'mousemove'
        },
        series: [
          {
            type: 'tree',
            data: [{
              "name": "",
              "children": [
                {
                  "name": "",
                  "children": [
                    {
                      "name": "",
                    },
                    {
                      "name": "HTTP",
                    },
                    {
                      "name": "",
                    }
                  ]
                },
                {
                  "name": "",
                  "children": [
                    {"name": "HTML",},
                    {"name": "",}
                  ]
                },
                {
                  "name": "",
                  "children": [
                    {"name": "CSS", },
                    {"name": "", },

                  ]
                },
                {
                  "name": "",
                  "children": [
                    {"name": "JavaScript",},
                    {"name": "JavaScriptDOM"},
                    {"name": "Ajax",}
                  ]
                },
                {
                  "name": "",
                  "children": [
                    {"name": "npm"},
                    {"name": "yarn",},
                  ]
                },
                {
                  "name": "",
                  "children": [
                    {"name": "React.js",},
                    {"name": "Vue.js",},
                    {"name": "Angular",},
                  ]
                }
              ]
            }],
            top: '5%',
            left: '7%',
            bottom: '1%',
            right: '40%',
            symbolSize: 13,
            label: {
              position: 'left',
              verticalAlign: 'middle',
              align: 'right',
              fontSize: 13
            },
            leaves: {
              label: {
                position: 'right',
                verticalAlign: 'middle',
                align: 'left'
              }
            },
            emphasis: {
              focus: 'descendant'
            },
            expandAndCollapse: true,
            animationDuration: 550,
            animationDurationUpdate: 750
          }
        ]
      });
      myChart.on('click', function(params) {
      });
    }
  },
  mounted () {
    this.initCharts();
  }
}
</script>

